@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  box-shadow: none;
  border-width: 0;
  outline: none;
  font-family: "Lato", sans-serif;
}

body {
  background-color: rgba(22, 21, 21, 0.8);
}

.join-wrapper {
  margin: 20vh auto;
  height: 60vh;
  width: 40vw;
  text-align: center;
  background-color: rgb(137, 200, 255);
  padding: 10px;
  border-radius: 10px;
  box-shadow: 1px 3px 20px rgba(0, 0, 0, 0.6);
  -webkit-animation: top-down 0.6s ease-out;
          animation: top-down 0.6s ease-out;
}

h1 {
  margin: 30px 0 40px;
  padding-bottom: 10px;
  border-bottom: 2px solid black;
}

.join-form {
  height: 100%;
  width: 80%;
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 auto;
}

.join-form__info {
  width: 80%;
  height: 30px;
  border-radius: 10px;
  padding: 5px;
  margin: 0px auto 20px;
  font-size: 18px;
}

.join-form__info:focus {
  border-inline-style: none;
}

.join-btn {
  width: 100%;
  height: 40px;
  margin-top: 30px;
}

.join-btn-btn {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-color: rgb(51, 136, 111);
  color: white;
  box-shadow: 0 5px 20px rgb(60, 155, 238);
  font-size: 20px;
}

.join-btn-btn:hover {
  cursor: pointer;
}

@-webkit-keyframes top-down {
  0% {
    transform: translateY(-200px);
  }

  80% {
    transform: translateY(20px);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes top-down {
  0% {
    transform: translateY(-200px);
  }

  80% {
    transform: translateY(20px);
  }

  100% {
    transform: translate(0);
  }
}

.infobar-container {
  width: 100%;
  height: 10%;
  background-color: rgb(112, 112, 221);
  z-index: 100;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.infobar-container-left {
  margin-left: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.infobar-name {
  margin-left: 10px;
}

.infobar-container-right {
  margin-right: 10px;
}

online {
  height: 5px;
  width: 5px;
}

.chat-container {
  width: 50vw;
  height: 70vh;
  margin: 5% auto;
  background-color: white;
}

.input-container {
  width: 100%;
  height: 10%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.message-box {
  width: 90%;
  height: 100%;
  font-size: 16px;
  border-width: 1px;
  padding: 2px 5px;
  border-radius: 10px;
}

.chat-btn {
  width: 15%;
  height: 100%;
  margin-left: 10px;
  background-color: rgb(107, 107, 240);
  border-radius: 10px;
}

.chat-btn:hover {
  cursor: pointer;
}

.messages-container {
  width: 100%;
  height: 80%;
}

.admin-message-container {
  text-align: center;
  margin: 10px 0;
}

.message-box-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

.message-container {
  max-width: 100%;
  margin: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
}

.current,
.others {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.current {
  justify-content: flex-end;
  margin-left: 10px;
}

.current .message-container {
  background-color: rgb(134, 134, 233);
  color: black;
}

.others {
  justify-content: flex-start;
  margin-right: 10px;
}

.others .message-container {
  background-color: rgb(122, 118, 118);
  color: white;
}

.admin-message {
  color: #cccc;
  margin: 10px;
}

.user-name {
  color: #cccc;
}

